/* === Reset & Font === */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
}
/* === App Layout === */
.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

form {
  margin-top: auto; /* 確保送出區塊在底部 */
}

.chat-area {
  flex: 1;
  overflow-y: auto;
  padding-bottom: 1em;
}

/* Light Mode */
.app-container:not(.dark-mode) {
  background-color: #ffffff;
  color: #000000;
}

/* Dark Mode */
.app-container.dark-mode {
  background-color: #1e1e1e;
  color: #eee;
}

/* === Chat Messages === */
.message-self,
.message-other {
  padding: 10px 15px;
  max-width: 80%;
  border-radius: 12px;
  word-break: break-word;
  line-height: 1.6;
  transition: all 0.3s ease;
}

.message-system {
  background-color: #ddd;
  font-style: italic;
  color: #555;
  text-align: center;
  padding: 5px 0;
  margin: 10px 0;
  transition: all 0.3s ease;
}

/* Message Colors (Light Mode) */
.message-self {
  background-color: #d1e7dd;
  margin-left: auto;
}

.message-other {
  background-color: #f1f1f1;
  margin-right: auto;
}

/* Message Colors (Dark Mode) */
.app-container.dark-mode .message-self {
  background-color: #357960;
  color: white;
}

.app-container.dark-mode .message-other {
  background-color: #444;
  color: white;
}

.app-container.dark-mode .message-system {
  background-color: #333;
  color: #aaa;
}

/* === Mode Toggle Button === */
.mode-toggle-button {
  position: fixed;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 999;
}

.mode-toggle-button i {
  font-size: 24px;
  transition: color 0.3s ease, transform 0.2s ease;
}

/* Toggle Icon Colors */
.app-container.dark-mode .mode-toggle-button i {
  color: #fff;
}

.app-container:not(.dark-mode) .mode-toggle-button i {
  color: #000;
}

.mode-toggle-button:hover i {
  transform: scale(1.1);
}

/* === Form Elements === */
input {
  background-color: transparent;
  border: 1px solid #ccc;
  border-radius: 20px;
  padding: 10px 15px;
  font-size: 1em;
  width: 100%;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  outline: none;
}

input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
}

.app-container.dark-mode input {
  border: 1px solid #555;
  color: white;
}

button {
  padding: 8px 12px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #ccc;
}

button {
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* === System Preference (Fallback only) === */
@media (prefers-color-scheme: dark) {
  body {
    background-color: #1e1e1e;
    color: #eee;
  }
}